import style from "../AdmissionNotification.module.css";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import { useState } from "react";
const ClassAllottmentStatusCard = ({ list }) => {
  const { t } = useTranslation();
  const [isOpen, setIsOpen] = useState(false);

  const onToggle = () => {
    setIsOpen((pre) => !pre);
  };

  return (
    <>
      <section className={style.as_container}>
        <div className={style.as_sub_container} onClick={onToggle}>
          <div className={style.as_container_inner}>
            <img
              src={
                list?.length > 0
                  ? `${assestsUtilityUrl}/your-radio-blue.svg`
                  : `${assestsUtilityUrl}/your-radio-grey.svg`
              }
              alt="icon"
            />
            <h6>{t("class_allottment")}</h6>
          </div>
          <img src={`${assestsUtilityUrl}/dropdown.svg`} alt="icon" />
        </div>
        {isOpen &&
          list?.map((message) => (
            <section className={style.as_another_container} key={message?._id}>
              <p>
                Welcome to
                <span
                  style={{
                    fontWeight: "500",
                    color: "#121212",
                  }}
                >
                  {" "}
                  {message?.classes?.department?.dName ?? ""} {" - "}
                  {message?.classes?.classTitle ?? ""}{" "}
                </span>
                Enjoy your Learning.
              </p>
            </section>
          ))}
      </section>
    </>
  );
};

export default ClassAllottmentStatusCard;
