import { useTranslation } from "react-i18next";
import style from "../AdmissionNotification.module.css";
import { useNavigate, useParams } from "react-router-dom";
import { memo, useCallback } from "react";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useUserMemberAppliedAllApplicationAccount } from "../../../Hooks/ApiHooks/UserMember/user-member-api";
import { useSelector } from "react-redux";
import Loading from "../../../Loader/Loading";
import YourAdmissionCardListCard from "./YourAdmissionCardListCard";

const YourAdmissionCardList = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const params = useParams();
  const id = useSelector((state) => state.authChange.id);
  const onNavigateProcess = useCallback(() => {
    navigate(`/q/${params.username}/member/your/admission?type=process`);
  }, [params.username, navigate]);

  const {
    userMemberAppliedAllApplicationAccount,
    userMemberAppliedAllApplicationAccountLoading,
  } = useUserMemberAppliedAllApplicationAccount({
    uid: id,
    skip: !id,
  });

  // console.info(
  //   "userMemberAppliedAllApplicationAccount",
  //   userMemberAppliedAllApplicationAccount
  // );
  return (
    <>
      <div className={style.yaclp_container} onClick={onNavigateProcess}>
        <img
          src={`${assestsFeedUrl}/your-admission.svg`}
          alt="your icon"
          style={{
            borderRadius: "0",
          }}
        />
        <div className={style.ufu_your_application_inner}>
          <h6>{t("apply_from_here_for_new_admission")}</h6>
          <p>{t("click_here_to_know_process")}</p>
        </div>
      </div>

      {userMemberAppliedAllApplicationAccount?.map((process) => (
        <YourAdmissionCardListCard process={process} key={process?._id} />
      ))}
      {userMemberAppliedAllApplicationAccountLoading && <Loading />}
    </>
  );
};

export default memo(YourAdmissionCardList);
