import React from "react";
import NavbarConfig from "../../../Shared/Navbar/NavbarConfig";
import GoBack from "../../../Shared/StaffStudentProfile/GoBack";
import StaffStudentProfileContainer from "../../../Shared/StaffStudentProfile/StaffStudentProfileContainer";
import YourAdmissionCardList from "./YourAdmissionCardList";
import StudentProfileHeader from "../../../Shared/StaffStudentProfile/StudentProfileHeader";
import { useTranslation } from "react-i18next";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useSearchParams } from "react-router-dom";
import YourAdmissionProcess from "./YourAdmissionProcess";
import AdmissionStatus from "../AdmissionStatus/AdmissionStatus";

const YourAdmissionConfig = () => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  return (
    <NavbarConfig>
      <GoBack
        customStyle={{
          width: "1180px",
        }}
      />
      <StaffStudentProfileContainer
        customStyle={{
          width: "1180px",
          paddingInline: "1rem",
          paddingBottom: "1rem",
        }}
      >
        {!searchParams.get("type") ? (
          <>
            <StudentProfileHeader
              title={t("your_admission")}
              customStyleContainer={{
                paddingLeft: "0",
              }}
            />
            <BorderBottom
              customStyleBorder={{
                marginTop: "0",
                marginBottom: "0.9rem",
              }}
            />
            <YourAdmissionCardList />
          </>
        ) : searchParams.get("type") === "process" ? (
          <>
            <StudentProfileHeader
              title={t("new_admission")}
              customStyleContainer={{
                paddingLeft: "0",
              }}
            />
            <BorderBottom
              customStyleBorder={{
                marginTop: "0",
                marginBottom: "0.9rem",
              }}
            />
            <YourAdmissionProcess />
          </>
        ) : searchParams.get("type") === "application" ? (
          <>
            <StudentProfileHeader
              title={t("admission_status")}
              customStyleContainer={{
                paddingLeft: "0",
              }}
            />
            <BorderBottom
              customStyleBorder={{
                marginTop: "0",
                marginBottom: "0.9rem",
              }}
            />
            <AdmissionStatus />
          </>
        ) : null}
      </StaffStudentProfileContainer>
    </NavbarConfig>
  );
};

export default YourAdmissionConfig;
