import { useLocation } from "react-router-dom";
import { useUserMemberAppliedOneApplicationStatus } from "../../../Hooks/ApiHooks/UserMember/user-member-api";
import { useSelector } from "react-redux";
import Loading from "../../../Loader/Loading";
import style from "../AdmissionNotification.module.css";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import AdmissionApplicationStatusCard from "./AdmissionApplicationStatusCard";
import ClassAllottmentStatusCard from "./ClassAllottmentStatusCard";
import AdmissionConfirmationStatusCard from "./AdmissionConfirmationStatusCard";
import FeesPaymentStatusCard from "./FeesPaymentStatusCard";
import DocumentVerificationStatusCard from "./DocumentVerificationStatusCard";

const AdmissionStatus = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const id = useSelector((state) => state.authChange.id);

  const {
    userMemberAppliedOneApplicationStatus,
    userMemberAppliedOneApplicationStatusLoading,
  } = useUserMemberAppliedOneApplicationStatus({
    data: {
      appId: getQuery.state?.appId,
      uid: id,
    },
    skip: !getQuery.state?.appId,
  });
  // console.info(
  //   "userMemberAppliedOneApplicationStatus",
  //   userMemberAppliedOneApplicationStatus
  // );
  return (
    <>
      <div className={style.yap_container_status}>
        <img
          src={
            getQuery.state?.admissionAdmin?.institute?.insProfilePhoto
              ? `${imageShowUrl}/${getQuery.state?.admissionAdmin?.institute?.insProfilePhoto}`
              : `${assestsFeedUrl}/feed-user-avatar.svg`
          }
          alt="your icon"
          style={{
            borderRadius: "6px",
          }}
        />
        <div className={style.ufu_your_application_inner}>
          <h6>{getQuery.state?.admissionAdmin?.institute?.insName ?? ""}</h6>
          <p>
            {getQuery.state?.applicationName ?? ""} ({t("student")})
          </p>
        </div>
      </div>

      <AdmissionApplicationStatusCard
        list={userMemberAppliedOneApplicationStatus?.admission_application}
      />
      <DocumentVerificationStatusCard
        list={userMemberAppliedOneApplicationStatus?.document_verification}
      />
      <FeesPaymentStatusCard
        list={userMemberAppliedOneApplicationStatus?.fees_payment}
      />
      <AdmissionConfirmationStatusCard
        list={userMemberAppliedOneApplicationStatus?.admission_confirmation}
      />
      <ClassAllottmentStatusCard
        list={userMemberAppliedOneApplicationStatus?.class_allotment}
      />
      {userMemberAppliedOneApplicationStatusLoading && <Loading />}
    </>
  );
};

export default AdmissionStatus;
