import style from "../AdmissionNotification.module.css";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import { useState } from "react";
const AdmissionConfirmationStatusCard = ({ list }) => {
  const { t } = useTranslation();
  const [isOpen, setIsOpen] = useState(false);

  const onToggle = () => {
    setIsOpen((pre) => !pre);
  };
  // console.info("list", list);

  return (
    <>
      <section className={style.as_container}>
        <div className={style.as_sub_container} onClick={onToggle}>
          <div className={style.as_container_inner}>
            <img
              src={
                list?.length > 0
                  ? `${assestsUtilityUrl}/your-radio-blue.svg`
                  : `${assestsUtilityUrl}/your-radio-grey.svg`
              }
              alt="icon"
            />
            <h6>{t("admission_confirmation")}</h6>
          </div>
          <img src={`${assestsUtilityUrl}/dropdown.svg`} alt="icon" />
        </div>
        {isOpen &&
          list?.map((message) => (
            <section className={style.as_another_container} key={message?._id}>
              <p
                style={{
                  marginBlock: "0.5rem",
                }}
              >
                Your admission has been confirmed, You will be allotted your
                class shortly, Stay Updated!
              </p>
              <p>
                For cancellation and refund, contact the admission department.
              </p>
              <p
                style={{
                  marginBlock: "0.7rem",
                }}
              >
                Note: Stay tuned for further updates.
              </p>
            </section>
          ))}
      </section>
    </>
  );
};

export default AdmissionConfirmationStatusCard;
