import style from "../AdmissionNotification.module.css";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import { useState } from "react";
import CreateButton from "../../../Ui/Button/CreateButton";
import StudentAdmissionPayFees from "../../../UserStaffMember/StudentAdmissionFees/PayFees/StudentAdmissionPayFees";
import StudentAdmissionFeesStatusReceipt from "../../../UserStaffMember/TransactionReceipt/Admission/StudentAdmissionFeesStatusReceipt";
import { useStudentAdmissionFeeCardBankValidate } from "../../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import NotifyEvent from "../../../Utils/Snackbar/NotifyEvent";
import StudentHostelPayFees from "../../../UserStaffMember/StudentAdmissionFees/PayFees/StudentHostelPayFees";
const FeesPaymentStatusCard = ({ list }) => {
  const { t } = useTranslation();
  const [isOpen, setIsOpen] = useState(false);
  const [pay, setPay] = useState(false);
  const [disabled, setDisabled] = useState(false);
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });

  const [studentAdmissionFeeCardBankValidate] =
    useStudentAdmissionFeeCardBankValidate();

  const onToggle = () => {
    setIsOpen((pre) => !pre);
  };
  // console.info("list", list);
  const onPayFees = () => {
    let fee_pay = list?.[0];
    if (fee_pay?.applicationId?._id && fee_pay?.finance) {
      setDisabled((pre) => !pre);
      studentAdmissionFeeCardBankValidate({
        fid: fee_pay?.finance,
        appId: fee_pay?.applicationId?._id,
        flow: fee_pay?.is_hostel ? "BY_HOSTEL" : "BY_DEPARTMENT",
      })
        .then((res) => {
          setDisabled((pre) => !pre);
          if (res?.data?.denied) {
            setSnackbar({
              open: true,
              msg: "Account is not activated",
              variant: "error",
            });
          } else {
            setPay(true);
          }
        })
        .catch({});
    }
  };
  return (
    <>
      <section className={style.as_container}>
        <div className={style.as_sub_container} onClick={onToggle}>
          <div className={style.as_container_inner}>
            <img
              src={
                list?.length > 0
                  ? `${assestsUtilityUrl}/your-radio-blue.svg`
                  : `${assestsUtilityUrl}/your-radio-grey.svg`
              }
              alt="icon"
            />
            <h6>{t("fees_payment")}</h6>
          </div>
          <img src={`${assestsUtilityUrl}/dropdown.svg`} alt="icon" />
        </div>
        {isOpen &&
          list?.map((message) => (
            <section className={style.as_another_container} key={message?._id}>
              <p
                style={{
                  marginBlock: "0.5rem",
                }}
              >
                Your documents are submitted and verified successfully
              </p>
              <p
                style={{
                  marginBlock: "0.9rem",
                }}
              >
                Complete your admission by paying applicable admission fees from
                below:
              </p>
              <p
                style={{
                  marginBlockEnd: "0.8rem",
                }}
              >
                Applicable Admission Fees: Rs.
                <span
                  style={{
                    fontWeight: "500",
                    color: "#121212",
                  }}
                >
                  {message?.remaining_list?.applicable_card
                    ?.remaining_array?.[0]?.remainAmount ?? 0}
                </span>
              </p>
              {message?.payment_status === "Not Paid" ? (
                <CreateButton label="pay_admission_fees" onAction={onPayFees} />
              ) : (
                <>
                  <p>Your fees payment is successful.</p>
                  <StudentAdmissionFeesStatusReceipt
                    rid={message?.fee_receipt?._id}
                    receiptFile={message?.fee_receipt?.receipt_file}
                  />
                </>
              )}
            </section>
          ))}
      </section>
      {pay && (
        <>
          {list?.[0]?.is_hostel ? (
            <StudentHostelPayFees
              onClose={() => setPay(false)}
              admission={list?.[0]?.remaining_list}
              install={
                list?.[0]?.remaining_list?.applicable_card?.remaining_array?.[0]
              }
              isEditable={
                list?.[0]?.student?.online_amount_edit_access === "Granted"
                  ? true
                  : false
              }
              statusId={list?.[0]?._id}
              isStatus
            />
          ) : (
            <StudentAdmissionPayFees
              onClose={() => setPay(false)}
              admission={list?.[0]?.remaining_list}
              install={
                list?.[0]?.remaining_list?.applicable_card?.remaining_array?.[0]
              }
              isEditable={
                list?.[0]?.student?.online_amount_edit_access === "Granted"
                  ? true
                  : false
              }
              statusId={list?.[0]?._id}
              isStatus
            />
          )}
        </>
      )}
      {disabled && <QvipleLoader />}
      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default FeesPaymentStatusCard;
